body, header, ul, a {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  --active-item-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  min-width: 1024px;
  height: 60px;
  padding: 0 50px;
  display: block;
  position: relative;
}

.container {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.logo-link {
  font-size: 0;
}

.nav {
  align-items: center;
  height: 60px;
  display: flex;
}

.nav-list {
  align-items: center;
  height: 60px;
  list-style: none;
  display: flex;
}

.nav-link {
  color: #000;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  line-height: 60px;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.nav-link:hover {
  -webkit-text-decoration: underline var(--active-item-color);
  text-decoration: underline var(--active-item-color);
  cursor: pointer;
}

.nav-list li:first-child .nav-link {
  margin-left: 0;
}

.is-active {
  color: var(--active-item-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-item-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

